<template>
  <div v-if="currentSong === undefined" id="intro" ref="intro">
    <div v-if="pageLoaded && cerditoLoaded" class="intro-container">
      <h1>
        ¡ Llegó el <span>Cerdito Mágico Rasta </span>!
      </h1>
      <p>
        Llito le contó al Cerdito Mágico sobre una lista de canciones de reggae🌴<br> que, con mucho amor, le hacen pensar en Llita 🐥🐤💕<br><br>
        El Cerdito Mágico las escuchó y, ¡les gustó tanto que se volvió Rasta!,  <br>
        y hoy con mucho esmero te las quiere mostrar 😍 <br>
      </p>
      <p class="desktop-only">
        Usa los botones y el scroll del mouse para mover y girar al Cerdito Mágico:
        <br>
        <img src="../assets/mouse.png" alt="">
      </p>
      <p class="desktop-only">
        Ahora, ¡elige una canción y baila junto al <span>Cerdito Mágico Rasta</span>!
      </p>
    </div>
    <div v-else class="intro-container">
      <h1>
        Cargando...
      </h1>
    </div>
  </div>
  <div id="canvas" ref="canvas" class="has-bg"></div>
  <video ref="video"  width="400" >
    <source v-if="currentSong === 0 || currentSong > 0" source :src="songs[currentSong]?.src" type="video/mp4">
    Your browser does not support HTML video.
  </video>
  <!-- Son name and artist -->
  <div class="song-name" ref="songName">
    <h1>{{songs[currentSong]?.title}}</h1>
    <h2>{{songs[currentSong]?.artist}}</h2>
  </div>
  <!-- list of songs -->
  <div 
    v-if="pageLoaded && cerditoLoaded"
    :class="{songs:true, show: true, 'with-intro':currentSong === undefined}" 
    ref="songs"
  >
    <div
      v-for="song in songs" :key="song.id" @click="setSong(song.id)"
      :class="{active: currentSong === song?.id, played: song.played, song: true}"
    >
      <div class="song_title">{{ song.title }}</div>
      <div class="song_artist">{{ song.artist }}</div>
    </div>    
  </div>
  <div id="palm1"></div>
  <div id="palm2"></div>
</template>

<script>
import * as THREE from 'three'
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
import * as Postprocessing from 'postprocessing'
import TrackballControls from 'three-trackballcontrols'
import * as TWEEN from '@tweenjs/tween.js'
// import sass
import '../assets/sass/particles.scss'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data: function () {
    return {
      controlsSpeedMultiplier: 1,
      bounceSpeedMultiplier : 0.9,
      bounceCerditoDelay : 0,
      currentSong: undefined,
      cerditoAlreadyBouncing: false,
      pageLoaded: false,
      cerditoLoaded: false,
      songs:[
        {
          id: 0,
          title: 'Felicidad',
          artist: 'Gondwana',
          src: require('../assets/videos/song1.mp4'),
          speedMultiplier: 0.84,
          delay: 14000, // 14000
          lightColor: 0x2974ac,
          ambientColor: 0x404040,
          played: false
        },
        {
          id: 1,
          title: 'Mi Amor',
          artist: 'Dread Mar I',
          src: require('../assets/videos/song2.mp4'),
          speedMultiplier: 0.72,
          delay: 56000, //56000
          lightColor: 0x1b41db,
          ambientColor: 0x3a454d,
          played: false
        },
        {
          id: 2,
          title: 'Mi Princesa',
          artist: 'Gondwana',
          src: require('../assets/videos/song3.mp4'),
          speedMultiplier: 0.69,
          delay: 36000, // 37000
          lightColor: 0x2974ac,
          ambientColor: 0x404040,
          played: false
        },
        {
          id: 3,
          title: 'Tus Ojos',
          artist: 'Los Cafres',
          src: require('../assets/videos/song4.mp4'),
          speedMultiplier: 0.68,
          delay: 0,
          lightColor: 0xdfca67,
          ambientColor: 0x706b58,
          played: false
        },
        {
          id: 4,
          title: 'Me Quedo Contigo (Si Me Das a Elegir) ',
          artist: 'Manu Chao & Radio Bemba',
          src: require('../assets/videos/song5.mp4'),
          speedMultiplier: 0.7,
          delay: 7600, // 7600
          lightColor: 0xd62000,
          ambientColor: 0x826863,
          played: false
        }
      ]
    }
  },
  created: function () {
    this.init();
  },
  mounted: function () {
    this.$refs.canvas.appendChild(this.renderer.domElement)
    this.lateInit();
    this.animate()
    window.addEventListener('load', () => {
      this.pageLoaded = true;
    })
  },
  watch: {
    currentSong: function (newVal, oldVal) {
      console.log('song changed', newVal, oldVal);

      this.$refs.video.classList.add('show');
      this.$refs.video.load();
      this.$refs.video.play();

      this.$refs.songName.classList.add('show');
      setTimeout(() => {
        this.$refs.songName.classList.remove('show');
      }, 10000);
      this.$refs.songs.classList.remove('show');
      this.$refs.canvas.classList.remove('has-bg');
      // hide intro using style
      if(newVal === 0 || newVal > 0){
        this.bounceSpeedMultiplier = this.songs[newVal].speedMultiplier;
        this.bounceCerditoDelay = this.songs[newVal].delay;
        this.lightBg.color.setHex(this.songs[newVal].lightColor);
        this.lightAmbient.color.setHex(this.songs[newVal].ambientColor);
        this.songs[newVal].played = true;
        
        if(!this.cerditoAlreadyBouncing){
          this.cerditoAlreadyBouncing = true;
          this.bounceCerdito();
        }
      }
      this.controls.reset();

    }
  },
  methods: {
    init() {
      this.scene = new THREE.Scene()
      this.camera = new THREE.PerspectiveCamera(
        75,
        window.innerWidth / window.innerHeight,
        0.1,
        1000
      )
      this.renderer = new THREE.WebGLRenderer({
        powerPreference: "high-performance",
        antialias: true,
        alpha: true,
        stencil: false,
        depth: false
      })
      this.renderer.setClearColor( 0x000000, 0 );
      
      // Postprocessing 
      this.postprocessingInit();
      
      this.light = new THREE.DirectionalLight('hsl(0, 100%, 100%)', .75)
      this.lightBg = new THREE.DirectionalLight('#AAAA00', 1)
      this.light3 = new THREE.DirectionalLight('#FFFFFF', .5)
      this.lightAmbient = new THREE.AmbientLight( 0x404040 ); // soft white light
      this.geometry = new THREE.BoxGeometry(1, 1, 1)
      this.material = new THREE.MeshStandardMaterial({
        side: THREE.FrontSide,
        color: 'hsl(0, 100%, 50%)',
        wireframe: false
      })
      this.loadObject();
      //this.cube = new THREE.Mesh(this.geometry, this.material)
      // this.axes = new THREE.AxesHelper(5)
      this.controls = []
    },
    postprocessingInit: function () {
      
      this.composer = new Postprocessing.EffectComposer(this.renderer)
      this.composer.addPass(new Postprocessing.RenderPass(this.scene, this.camera))
      // Bloom
      this.composer.addPass(new Postprocessing.EffectPass(
        this.camera,
        new Postprocessing.BloomEffect({
          luminanceThreshold: 0.01,
          radius: 10,
          intensity: 0.1
        })
      ));

      // Antialiasing
      const smaaEffect = new Postprocessing.SMAAEffect()
      smaaEffect.preset = Postprocessing.SMAAPreset.ULTRA
      smaaEffect.edgeDetectionMode = Postprocessing.EdgeDetectionMode.COLOR
      this.composer.addPass(new Postprocessing.EffectPass(
        this.camera,
        smaaEffect
      ))
    },
    lateInit: function () {
      this.scene.add(this.camera)
      this.scene.add(this.light)
      this.scene.add(this.lightBg)
      this.scene.add(this.light3)
      this.scene.add(this.lightAmbient)
      this.renderer.setSize(window.innerWidth, window.innerHeight)
      this.composer.setSize(window.innerWidth, window.innerHeight)
      this.light.position.set(0, 0, 10)
      this.lightBg.position.set(3, 1, -5)
      this.light3.position.set(-1, -10, -1)
      this.camera.position.z = 2

      this.controls = new TrackballControls(this.camera, this.renderer.domElement)
      this.controls.rotateSpeed = 4 
      this.controls.zoomSpeed = 5
      this.controls.panSpeed = 0.8
      this.controls.noZoom = false
      this.controls.noPan = false
      this.controls.staticMoving = true
      this.controls.dynamicDampingFactor = 0.3


      const onWindowResize = () => {
          this.camera.aspect = window.innerWidth / window.innerHeight;
          this.camera.updateProjectionMatrix();

          this.renderer.setSize(window.innerWidth, window.innerHeight);
          this.composer.setSize(window.innerWidth, window.innerHeight)
      }
      window.addEventListener( 'resize', onWindowResize, false );


    },
    loadObject: async function () {
      const loader = new GLTFLoader();
      this.cerdito = new THREE.Object3D();
      // await loader.loadAsync( require('../assets/models/EduDeveloper.glb'), function () {
      await loader.loadAsync( require('../assets/models/cerdito_rasta_optimized.glb'), function () {
      }).then( r => {
          this.cerdito = r.scene;
          this.cerdito.scale.set(0.4, 0.4, 0.4);
          this.cerdito.position.x = 0.5;
          this.cerdito.rotation.y = -0.15;
          this.scene.add(this.cerdito);
          this.cerditoLoaded = true;
      });
    },
    bounceCerdito: async function () {
      const resetEveryMs = 8000 * this.bounceSpeedMultiplier;

      // move side to side
      const moveLeft = {
        x: '-0.1',
      }
      const moveRight = {
        x: '+0.1',
      }
      const leftHorizontal = new TWEEN.Tween(this.cerdito.position)
        .to(moveLeft, 1000 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Sinusoidal.InOut)
      const rightHorizontal = new TWEEN.Tween(this.cerdito.position)
        .to(moveRight, 1000 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Sinusoidal.InOut)
      
      leftHorizontal.chain(rightHorizontal)
      rightHorizontal.chain(leftHorizontal)
      
      if(this.bounceCerditoDelay > 0){
        this.cerdito.position.y = 0;
        this.cerdito.rotation.y = -0.15;
        this.cerdito.rotation.z = 0;
        leftHorizontal.start();
      }
      let awaitedDelay = 0;
      let done = false;
      while (!done) {
        if(awaitedDelay >= this.bounceCerditoDelay || 0){
          done = true;
        }else{
          await new Promise(resolve => setTimeout(resolve, 1000));
          awaitedDelay += 1000;
        }
      }
      this.bounceCerditoDelay = 0;
      leftHorizontal.stop();


      // move in the screen side to side
      const moveScreenLeft = {
        x: '-1',
      }
      const moveScreenRight = {
        x: '+1',
      }

      const rightScreen = new TWEEN.Tween(this.cerdito.position)
        .to(moveScreenRight, 4000 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Quadratic.InOut)
      const leftScreen = new TWEEN.Tween(this.cerdito.position)
        .to(moveScreenLeft, 4000 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Quadratic.InOut)

      leftScreen.chain(rightScreen)
      rightScreen.chain(leftScreen)
      leftScreen.start();

      const rotateHorizontalLeft = {
        y: '+0.7',
      }
      const rotateHorizontalRight = {
        y: '-0.7',
      }
      const leftRotationHorizontal = new TWEEN.Tween(this.cerdito.rotation)
        .to(rotateHorizontalLeft, 1000 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Sinusoidal.InOut)

      const rightRotationHorizontal = new TWEEN.Tween(this.cerdito.rotation)
        .to(rotateHorizontalRight, 1000 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Sinusoidal.InOut)

      leftRotationHorizontal.chain(rightRotationHorizontal)
      rightRotationHorizontal.chain(leftRotationHorizontal)

      leftRotationHorizontal.start();

      // Up down
      const moveUp = {
        y: '+0.07',
      }
      const moveDown = { 
        y: '-0.07',
      }
      const up = new TWEEN.Tween(this.cerdito.position)
        .to(moveUp, 500 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Sinusoidal.InOut)

      const down = new TWEEN.Tween(this.cerdito.position)
        .to(moveDown, 500 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Sinusoidal.InOut)
        
      up.chain(down);
      down.chain(up);

      up.start();

      // Rotate left right
      const rotateLeft = {
        z: '+1',
      }
      const rotateRight = { 
        z: '-1',
      }
      const left = new TWEEN.Tween(this.cerdito.rotation)
        .to(rotateLeft, 1000 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Quadratic.InOut)

      const right = new TWEEN.Tween(this.cerdito.rotation)
        .to(rotateRight, 1000 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Quadratic.InOut)

      this.cerdito.rotation.z = -0.5;

      left.chain(right);
      right.chain(left);

      left.start();
    
      // widen
      const wide = {
        x: '+0.04',
        y: '-0.05',
      }
      const narrow = { 
        x: '-0.04',
        y: '+0.05',
      }
      const wideTween = new TWEEN.Tween(this.cerdito.scale)
        .to(wide, 500 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Linear.None)

      const narrowTween = new TWEEN.Tween(this.cerdito.scale)
        .to(narrow, 500 * this.bounceSpeedMultiplier)
        .easing(TWEEN.Easing.Linear.None)

      this.cerdito.scale.x = 0.4;
      this.cerdito.scale.y = 0.4;

      narrowTween.chain(wideTween);
      wideTween.chain(narrowTween);

      narrowTween.start();

      setTimeout(() => {
        // refrescar cada cierto tiempo las animaciones
        // Para obtener el nuevo valor de la velocidad
        leftHorizontal.stop();
        rightHorizontal.stop();
        leftScreen.stop();
        rightScreen.stop();
        leftRotationHorizontal.stop();
        rightRotationHorizontal.stop();
        up.stop();
        down.stop();
        left.stop();
        right.stop();
        narrowTween.stop();
        wideTween.stop();
        this.bounceCerdito();
      }, resetEveryMs);

    },
    setSong: function (val) {
      if(this.currentSong == val){
        this.$refs.video.play();
      }
      this.currentSong = val;
    },
    animate: function (t) {
      requestAnimationFrame(this.animate)
      // this.renderer.render(this.scene, this.camera)
      this.composer.render(this.scene, this.camera)
      if(this.cerdito) {
        TWEEN.update(t);
      }
      this.controls.update()
    }
  },
}
</script>

